<template>
	<div class="star" :class='starType'>
		<span v-for='(itemClass,k) in itemClasses' :class='itemClass' class="star-item" :key="k"></span>
	</div>
</template>

<script>
	
	const LENGTH = 5;
	const ON   = 'on';
	const OFF  = 'off';
	const HALF = 'half'

	export default{
		props : [
			'score',
			'size'
		],
		computed : {
			starType(){
				return 'star-'+this.size;
			},
			itemClasses(){
				// 定义空数组
				let result = [];
				// 取整 3.6 this.scroe
				let integer = Math.floor( this.score ); //3
				let decimal = this.score - integer;		//0.6

				for(let i =0 ; i < integer ;i++){
					result.push( ON );
					// result[ON,ON,ON]
				}
				if( decimal > 0.5 ){
					result.push( HALF );
					// result[ON,ON,ON,HALF]
				}
				while( result.length < LENGTH ){
					result.push( OFF );
					// result[ON,ON,ON,HALF,OFF]
				}
				return result;
			}
		}

	}
</script>

<style scoped lang='less'>
	.star{
		&.star-small{
			.star-item{
				display: inline-block;
				width: 0.2rem;
				height: 0.2rem;
				margin-right: 0.1rem;
				&.on{
					background-image: url('../../../static/img/star_on.png');
					background-size: 0.2rem 0.2rem;
					background-repeat: no-repeat;
				}
				&.half{
					background-image: url('../../../static/img/star_half.png');
					background-size: 0.2rem 0.2rem;
					background-repeat: no-repeat;
				}
				&.off{
					background-image: url('../../../static/img/star_off.png');
					background-size: 0.2rem 0.2rem;
					background-repeat: no-repeat;
				}
			}
        }
        &.star-big{
           .star-item{
                display: inline-block;
                width: 0.42rem;
                height: 0.42rem;
                margin-right: 0.24rem;
                &:last-child{
                    margin: 0;
                }
                &.on{
                    background-image: url('../../../static/img/star_on.png');
                    background-size: 0.42rem 0.42rem;
                    background-repeat: no-repeat;
                }
                &.half{
                    background-image: url('../../../static/img/star_half.png');
                    background-size: 0.42rem 0.42rem;
                    background-repeat: no-repeat;
                }
                &.off{
                    background-image: url('../../../static/img/star_off.png');
                    background-size: 0.42rem 0.42rem;
                    background-repeat: no-repeat;
                }
            } 
        }
		.star-item{
			display: inline-block;
			width: 0.28rem;
			height: 0.28rem;
			margin-right: 0.1rem;
			&.on{
				background-image: url('../../../static/img/star_on.png');
				background-size: 0.28rem 0.28rem;
				background-repeat: no-repeat;
			}
			&.half{
				background-image: url('../../../static/img/star_half.png');
				background-size: 0.28rem 0.28rem;
				background-repeat: no-repeat;
			}
			&.off{
				background-image: url('../../../static/img/star_off.png');
				background-size: 0.28rem 0.28rem;
				background-repeat: no-repeat;
			}
		}
	}
</style>


