<template>
  <div class="footer">
      <div class="cart" :class="{'hasCount':totalCount}"  @click='showCartDetail(selectFoods)'><i class="icon iconfont icon-gouwuche" ></i></div>
      <div class="bg"></div>
      <div class="totalCount" v-show="totalCount">{{totalCount}}</div>
      <div class="footerContent">
          <div class="footerPrice">
              <span class="footerPriceTotal">¥{{totalPrice}}</span>
              <span class="footersendDeliveryPrice">另需配送费¥{{seller.deliveryPrice}}元</span>
          </div>
          <div class="footerPriceminPrice">
              <span >{{payDesc}}</span>
          </div>
      </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
    ...mapGetters([
				'goods',
				'seller'
	]),
    computed:{
             ...mapGetters([
				'goods',
				'seller'
	        ]),
        	selectFoods(){
				let foods = [];
				// 遍历大的分类
				this.goods.forEach( good => {
					// 遍历分类里面的每一个商品
					good.foods.forEach( food => {
						// 如果商品的count大于0  就放进定义的数组中去
						if( food.count > 0 ){
							foods.push(food);
						}
					})
				});
				// dom是数据的自然映射，数据改变则dom随之更新
				return foods;
			},
			totalPrice(){
				let total = 0;
				// 对上面的计算属性进行遍历（就是对数组遍历，里面的每一个对象都有count）
				this.selectFoods.forEach( food => {
					total += food.count * food.price;
				});
				return total;
			},
			totalCount(){
				let count = 0 ;
				this.selectFoods.forEach( food => {
					count += food.count;
				});
				return count;
			},
			payDesc(){
				if( this.totalPrice === 0){
					return `￥${this.seller.minPrice}元起送`;
				}else if( this.totalPrice < this.seller.minPrice ){
					return `还差￥${this.seller.minPrice - this.totalPrice}元起送`
				}else{
					return "去结算";
				}
			}
    },
    methods:{
        showCartDetail( selectFoods ){
				this.$store.dispatch('showCartDetail',selectFoods);
		},
    }
};
</script>
<style lang="less" scoped>
    .footer {
        width: 100%;
        height: 1.16rem;
        position: fixed;
        bottom: 0;
        .totalCount{
            width: 0.48rem;
            height: 0.32rem;
            border-radius: 0.24rem;
            position: absolute;
            background-color: #F01414;
            color: rgb(255,255,255);
            font-size: 0.18rem;
            font-weight: 700;
            text-align: center;
            line-height: 0.32rem;
            z-index: 8;
            left: 0.88rem;
        }
        .bg{
            position: absolute;
            left: 0.52rem; 
            height: 1.04rem;
            bottom: 0;
            width: 0.60rem;;
            background-color: #141d27;
        }
        .cart {
            background-color: rgba(255, 255, 255,0.2);
            height: 1.12rem;
            width: 1.12rem;
            box-sizing: border-box;
            border: 0.12rem solid #141d27;
            border-radius: 0.56rem;
            text-align: center;
            position: absolute;
            left: 0.24rem;
            z-index: 3;
            
            i {
            font-size: 0.48rem;
            color: rgba(255, 255, 255, 0.8);
            line-height: 0.88rem;
            }
            &.hasCount{
                background-color:#00A0DC;
                color: rgb(255,255,255);
            }
        }
        
        .footerContent{
            background-color: #141d27;
            height: 0.96rem;
            position: absolute;
            bottom: 0;
            width: 100%; 
            padding-left: 1.60rem;
            box-sizing: border-box;
            color: rgba(255, 255, 255, 0.4);
            font-size: 0.32rem;
            line-height: 0.48rem;
            font-weight: 700;
            display: flex;
            vertical-align: center;
            .footerPrice{
                flex: 1;
                 padding: 0.24rem 0 ;
                 box-sizing: border-box;
                span{
                    display: inline-block;
                    
                }
                .footerPriceTotal{
                    padding-right: 0.12rem;
                    box-sizing: border-box;
                }
                .footersendDeliveryPrice{
                    font-size: 0.24rem;
                    font-weight:200;
                    padding-left: 0.12rem;
                    box-sizing: border-box;
                    border-left: 0.01rem  solid rgba(255,255,255,0.1);
                }
            }
            .footerPriceminPrice{
                font-size: 0.24rem;
                width: 2.1rem;
                background-color: rgba(255, 255, 255,0.2);
                text-align: center;
                padding: 0.24rem 0 ;
                box-sizing: border-box;
            }
        }
    }
</style>



