<template>
  <!-- 商家信息开始 -->
  <div class="sellerTotal" id="sellerTotal" >
    <div>
      <!-- 商家信息头 -->
      <div class="sellerTop">
        <div class="sellerTopHeader">
          <div class="sellerTopHeaderLeft">
            <!-- 商家名称 -->
            <div class="sellerName">{{seller.name}}</div>
            <!-- 星星评论 -->
            <div class="sellerStar">
              <star :score="seller.score"></star>
              <span>{{seller.ratingCount}}</span>
              <span >月售{{seller.sellCount}}单</span>
            </div>
          </div>
          <!-- 收藏 -->
          <div class="sellerTopHeaderRight">
            <!-- 爱心 -->
            <div class="heart"><i class="iconfont icon-aixin"></i></div>
            <div>已收藏</div>
          </div>
        </div>
        <!-- 起送价格及时间信息 -->
        <div class="sellerPrice">
          <div class="sellerPriceLeft">
            <div>起送价</div>
            <div><span>{{seller.minPrice}}</span><span>元</span></div>
          </div>
          <div class="sellerPriceLeft sellerPriceBorder">
            <div>商家配送</div>
            <div><span>{{seller.deliveryPrice}}</span><span>元</span></div>
          </div> 
          <div class="sellerPriceLeft sellerPriceBorder">
            <div>平均配送时间</div>
            <div><span>{{seller.deliveryTime}}</span><span>分钟</span></div>
          </div>                   
        </div>
      </div>
      <!-- 空白连接处 -->
      <div class="whiteSpace"></div>
      <!-- 商家公告 -->
      <div class="sellerNotice">
        <!-- 商家公告信息头 -->
        <div class="sellerNoticeInfo">
          <div class="sellerNoticeTitle">公告与活动</div>
          <div class="sellerNoticeContent">{{seller.bulletin}}</div>
        </div>
        <!-- 商家公告列表 -->
        <div class="sellerNoticeList" v-for="(v,k) in seller.supports" :key="k">
          <img :src="v | imgChoose" alt="">
          <span v-if="seller.supports">{{v.description}}</span>
        </div>
      </div>
      <!-- 空白连接处 -->
      <div class="whiteSpace"></div>
      <!-- 商家实景 -->
      <div class="sellerPics">
        <div class="sellerPicsTitle">商家实景</div>
        <div class="sellerPicsWrapper" ref="sellerPicsWrapper">
          <div class="sellerPicsList" ref="sellerPicsList">
            <div class="sellerPicsItem" v-for="(value,key) in seller.pics" :key="key">
              <img :src="value" alt="" >
            </div>         
          </div>
        </div>

      </div>
      <!-- 空白连接处 -->
      <div class="whiteSpace"></div>
      <!-- 商家信息 -->
      <div class="sellerInfo">
        <div class="sellerInfoTitle">商家信息</div>
        <div class="sellerInfoList" v-for="(v,k) in seller.infos" :key="k">{{v}}</div>
      </div>
      </div>
  </div>
</template>
<script>
import {mapGetters,mapActions} from "vuex";
import star from '../star/star'
import BScroll from 'better-scroll'
import imgChoose from '../../img'
export default {
  components:{
    star,
  },
  computed:{
    ...mapGetters([
        'seller',
        'ratings'
    ]),
  },
  mounted(){
    this._initPicture();
    	setTimeout( () => {
        this.$nextTick( () => {
          if( !this.sellerSCroll ){
            this.sellerSCroll = new BScroll( document.getElementById('sellerTotal') ,{
            });
          }else{
            this.sellerSCroll.refresh();
          }
        })
			},300)
  },
  watch:{
    seller(){
      this._initPicture();
    }
  },
  methods:{
    _initPicture(){
      if( this.seller.pics){
        let pw = 120;
        let m = 12;
        let w = ( pw + m ) * this.seller.pics.length - m;
        this.$refs.sellerPicsList.style.width = w + 'px';

        if( !this.picBScroll ){
          this.picBScroll = new BScroll( this.$refs.sellerPicsWrapper ,{
            scrollX : true ,
            eventPassthrough : 'vertical'
          })
        }else{
          this.picBScroll.refresh();
        }
      }

    }
  }
}
</script>
<style lang='less' scoped>
 /*商家信息开始*/
 .sellerTotal{
    /*商家信息头*/
    position: absolute;
    z-index: -100;
    width: 100%;
    top:3.48rem;
    bottom:1rem;
    overflow: hidden;
    .sellerTop{
      padding: 0 0.36rem;
      box-sizing: border-box;
      border-top: 0.01rem solid #e6e5e2;
      border-bottom: 0.01rem solid #e6e5e2;
      .sellerTopHeader{
        display: flex;
        justify-content: space-between;
        padding: 0.36rem 0;
        box-sizing: border-box;
        .sellerTopHeaderLeft{
          /*商家名称*/
          .sellerName{
            font-size: 0.28rem;
            color: rgb(7,17,27);
          }
          /*星星评论*/
          .sellerStar{
            margin-top: 0.16rem; 
            
            .star{
               display: inline-block; 
            }         
            span{
              font-size: 0.2rem;
              line-height: 0.28rem;
              color:rgb(77,85,93);
              display: inline-block;
              padding-top:-0.2rem;
              box-sizing: border-box; 
              vertical-align: top;
              margin-left: 0.12rem;
            }
          }
        }
        /*收藏*/
        .sellerTopHeaderRight{
          /*爱心*/    
          .heart{
            text-align: center;
            i{
              font-size: 0.48rem;
              color: rgb(240,20,20);
            }            
          }
          div:last-child{
            margin-top: 0.08rem;
            font-size: 0.2rem;
            color: rgb(77,85,93);
          }
          }
      }
      
      /*起送价格及时间信息*/
      .sellerPrice{
        border-top: 0.01rem solid #e6e5e2;
        padding: 0.36rem 0;
        box-sizing: border-box;
        display: flex;
        justify-content:space-between;
        .sellerPriceLeft{
          flex:1;
          text-align: center;
          div:first-child{
            font-size: 0.2rem;
            color: rgb(147,153,159);
          }
          div:last-child{
            margin-top: 0.08rem;
            font-size: 0.2rem;
            color: rgb(7,17,27);
            span:first-child{
              font-size: 0.48rem;
            }
          }
        }
        .sellerPriceBorder{
          border-left: 0.01rem solid #e6e5e2;
        }
      }
    }
    /*空白连接处*/
    .whiteSpace{
      height: 0.34rem;
      background-color: rgb(233,235,236);
      border-bottom:0.01rem solid #e6e5e2; 
      border-top:0.01rem solid #e6e5e2; 
    }
    .sellerNotice{
      padding: 0 0.36rem;
      box-sizing: border-box;
      /*商家公告信息头*/
      .sellerNoticeInfo{
        padding: 0.36rem 0 0.32rem 0; 
        box-sizing: border-box;
        .sellerNoticeTitle{
          font-size: 0.28rem;
          color:rgb(7,17,27);
        }
        .sellerNoticeContent{
          margin-top: 0.16rem;
          padding:0 0.24rem;
          box-sizing: border-box;
          font-size: 0.2rem;
          font-weight: 200;
          color:rgb(240,20,20);
          line-height: 0.48rem;
        }
      }
      /*商家公告列表*/
      .sellerNoticeList{
        padding: 0.32rem 0.24rem;
        border-top: 0.01rem solid #e6e5e2;
        img{
          width: 0.32rem;
          height:0.32rem;
        }
        span{
          margin-left: 0.04rem;
          font-size: 0.24rem;
          font-weight: 200;
          vertical-align: top;
          line-height: 0.32rem;
          color: rgb(7,17,27);
        }
      }
      
    }
    /*商家实景*/
    .sellerPics{
      padding: 0.36rem 0.84rem 0.36rem 0.36rem;
      border-top: 0.01rem solid #e6e5e2;
      border-bottom: 0.01rem solid #e6e5e2;     
      .sellerPicsTitle{
        font-size: 0.28rem;
        color:rgb(7,17,27);
      }
      .sellerPicsWrapper{
        margin-top: 0.24rem;
        white-space: nowrap;
        .sellerPicsList{
          .sellerPicsItem{
            display: inline-block;
            margin-right: 0.12rem;
            &:last-child{
              margin:0;
            }
            img{     
              height:1.80rem;
              width: 2.4rem;             
            }
          }
        
          
        }
      }
      
    } 
    /*商家信息*/
    .sellerInfo{
      padding: 0.36rem  0.36rem 0 0.36rem;
      border-top: 0.01rem solid #e6e5e2;
      border-bottom: 0.01rem solid #e6e5e2;  
      .sellerInfoTitle{
        font-size: 0.28rem;
        color:rgb(7,17,27);
        margin-bottom: 0.24rem;
      }
      .sellerInfoList{
        padding: 0.32rem 0.24rem;
        border-top: 0.01rem solid rgba(7,17,27,0.1);
        font-size: 0.24rem;
        font-weight: 200;
        line-height: 0.32rem;
        color: rgb(7,17,27);
      }
    } 
 }
</style>



