<template>
	<div class="goods">
		<div class="menu-wrap" ref='menuwrap'>
			<ul>
				<li class="menu-item" :class=" {'current': currentIndex==i}"  @click='selectMenu(i)' v-for='(val,i) in goods' :key="i">
					<span class="text">
						{{val.name}}
					</span>
				</li>
			</ul>
		</div>
		<div class="foods-wrap" ref='foodwrap'>
			<ul>
				<li v-for='(item,index) in goods' class="food-list food-list-hook" :key="index">
					<h1 class="title">{{ item.name }}</h1>
					<ul>
						<li v-for = '(food,key) in item.foods':key="key" class="food-item" >
							<div class="food-item-img" @click='showFoodDetail(food)'><img :src="food.icon" alt=""></div>
							<div class="food-item-info">
								<div class="food-item-info-name">{{food.name}}</div>
								<div class="food-item-info-description">{{food.description}}</div>
								<div class="food-item-info-Count">
									<span class="food-item-info-sellCount">月售{{food.sellCount}}份</span>
									<span class="food-item-info-rating">好评率{{food.rating}}%</span>
								</div>
								<div class="food-item-info-sell">
									<div>
										<span class="food-item-info-price">￥{{food.price}}</span>
										<span v-show="food.oldPrice" class="food-item-info-oldPrice">￥{{food.oldPrice}}</span>
									</div>
									<cartControl :food='food'></cartControl>
								</div>
							</div>
						</li>
					</ul>
				</li>
			</ul>
		</div>
		<foodDetail></foodDetail>
	</div>
</template>

<script>
	import {mapGetters,mapActions} from "vuex";
	import BScroll from 'better-scroll';
	import foodDetail from '../foodDetail/foodDetail.vue';
	import cartControl from '../cartControl/cartControl.vue'
	export default{
		components:{
			foodDetail,
			cartControl
		},
		computed:{
			...mapGetters([
				'goods',
				'ratings'
			]),
			currentIndex(){
				for(let i=0;i<this.heights.length;i++){
					let height1 = this.heights[i];
					let height2 = this.heights[i+1]-3;
					if(this.scrollY+2>=height1 && this.scrollY<height2){
						// console.log(i);
						return i;
						
					}
				}
				return 0;
			}
		},
		data(){
			return {
				scrollY:"",
				heights:[],
			}
		},
		mounted(){
			this.$nextTick( () => {
				setTimeout( () => {
						// 左边分类
					if( !this.menuwrapBScroll ){
						this.menuwrapBScroll = new BScroll( this.$refs.menuwrap,{
							click : true
						})
					}else{
						this.menuwrapBScroll.refresh();
					};
					// 右边分类详情
					if( !this.foodwrapBScroll ){
						this.foodwrapBScroll = new BScroll( this.$refs.foodwrap,{
							click : true,
							probeType : 3
						})
					}else{
						this.foodwrapBScroll.refresh();
					};
					this.foodwrapBScroll.on('scroll',(position)=>{
						// console.log('www')
						// console.log(position);
						this.scrollY=Math.abs(Math.round(position.y));
						// console.log(this.scrollY);
					});
					this.calcHeight()
				})
			})
		},
		methods:{
			showFoodDetail( food ){
				this.$store.dispatch('showFoodDetail',food);
			},
			calcHeight(){
				let foodList = document.getElementsByClassName('food-list-hook');
				// console.log(foodList)
				let height = 0;
				this.heights.push(height);
				for(let i=0;i<foodList.length;i++){ 
					
					height += foodList[i].clientHeight;
					this.heights.push(height);
				}
				// console.log(this.heights);
			},
			selectMenu(i){
				let foodList = document.getElementsByClassName('food-list-hook');
				this.foodwrapBScroll.scrollToElement(foodList[i],300)
			}
		}

	}
</script>

<style lang='less' scoped>
	.goods{
		position: absolute;
		display: flex;
		overflow: hidden;
		width: 100%;
		top:3.5rem;
		bottom: 1.16rem;
		.menu-wrap{
			/* 不扩展 不缩放 */
			flex: 0 0 1.6rem;
			width: 1.6rem;
			
			.menu-item{
				width: 1.12rem;
				height: 1.08rem;
				font-size: 0.24rem;
				background: #f3f5f7;
				color:rgb(7,17,27);
				padding: 0 0.24rem;
				font-weight: 200;
				line-height: 0.28rem;
				display: table;
				.text{
					width: 1.12rem;
					display: table-cell;
					vertical-align: middle;
					border-bottom: 1px solid rgba(7,17,27,0.1);
				}
			}
			.current{
				background-color: rgba(7,12,21,0.2);;
			}

		}
		.foods-wrap{
			flex:1;
			.food-list{
				.title{
					width: 100%;
					height: 0.52rem;
					line-height: 0.52rem;
					font-size: 0.24rem;
					color:rgb(147,153,159);
					background: rgba(7,17,27,0.1);
					padding-left: 0.28rem;
					box-sizing: border-box;
				}
				ul{
					width: 100%;
					padding:0 0.36rem;
					box-sizing: border-box;
					.food-item{
						padding: 0.36rem 0;
						border-bottom: 1px solid rgba(7,17,27,0.1);
						display: flex;
						.food-item-img{
							img{
								width: 1.14rem;
								height: 1.14rem;
							}
						}
						.food-item-info{
							flex: 1;
							padding-left:0.20rem; 
							box-sizing: border-box;
							font-size: 0.20rem;
							color:rgb(147,153,159);
							.food-item-info-name{
								font-size: 0.28rem;
								color:rgb(7,17,27);
							}
							.food-item-info-description{
								margin-top: 0.16rem;
							}
							.food-item-info-Count{
								margin-top: 0.16rem;
								span:first-child{
									margin-right: 0.08rem;
								}
							}
							.food-item-info-sell{
								margin-top: 0.16rem;
								display: flex;
								justify-content: space-between;
								.food-item-info-price{
									font-size: 0.28rem;
									line-height: 0.48rem;
									font-weight: 700;
									color: rgb(240,20,20);
									margin-right: 0.08rem;
								}
								.food-item-info-oldPrice{
									font-size: 0.2rem;
									line-height: 0.48rem;
									font-weight: 700;
									text-decoration: line-through;
									color: rgb(147,153,159);
								}

							}
						}
					}
				}
			}
		}
	}
</style>





