import axios from 'axios';
import TYPES from '../types.js'
const state ={
    ratings:[],
    selectRatings : [],
	types:['满意','不满意','全部'],
	selectType : 2,
	onlyContent : false
}
const getters={
    ratings(state){
        return state.ratings;
    },
    selectRatings( state ){
		return state.selectRatings;
	},
	types( state ){
		return state.types;
	},
	selectType( state ){
		return state.selectType;
	},
	onlyContent( state ){
		return state.onlyContent;
    }
    
}
const actions = {
    //获取ratings数据
    getRatings( {commit} ){
    //发送ajax请求 需要配代理
      axios.get('../../../data.json').then( res => {
          commit( TYPES.GET_RATINGS , res.data.ratings );
      })
  },
    // 点击筛选内容
	select( {commit} ,{type}){
		commit( TYPES.SELECT,{type} );
	},
	// 初始化状态
	changTypes({commit} ,_types){
		commit( TYPES.CHANGE_TYPES ,_types);
	},
	// 只看有内容的评价
	toggle({commit}){
		commit(TYPES.TOGGLE);
	}

}
const mutations={
    [TYPES.GET_RATINGS](state,data){
        state.ratings=data;
		state.selectRatings=data;
    },
    [TYPES.SELECT](state,{type}){
		// console.log(state.ratings);
		//点击谁就把谁的状态改变一下
		state.selectType = type;
		// console.log(type)
		//			需要遍历的数据			所有的评价 24
		state.selectRatings = state.ratings.filter( rating =>{
			
			if( state.selectType === 2 ){
				return true;
			}else{
				//          0 // 1         0   //  1 
				return rating.rateType == state.selectType;
			}
		})
		// console.log(state.ratings)
	},
	[TYPES.TOGGLE](state){
		state.onlyContent = !state.onlyContent;
		state.selectRatings = state.ratings.filter( rating =>{
			//   如果  我想看内容   但是  又没有内容  
			if( state.onlyContent && !rating.text ){
				return false;
			}else if( state.selectType === 2 ){
				return true;
			}else{
				//          0 // 1         0   //  1 
				return rating.rateType == state.selectType;
			}
		})
	},
	[TYPES.CHANGE_TYPES](state,_types){
		state.types = _types;
		state.selectType = 2;
		//		需要遍历的数据			24
		state.selectRatings = state.ratings;
		state.onlyContent = false;
	}
}

export default {
	state,
	getters,
	mutations,
	actions
}


