import axios from 'axios';
import TYPES from '../types.js'

const state = {
	cc:"1111",
	goods: [],
	detailShow: false,
	food: [],
	select:[],
	selectFlag:false

}

const getters = {
	goods(state){
		return state.goods;
	},
	detailShow( state ){
		return state.detailShow;
	},
	food( state ){
		return state.food;
	},
	select(state){
		return state.select;
	},
	selectFlag(state){
		return state.selectFlag;
	}
}

const actions = {
    //获取goods数据
	getGoods( {commit} ){
	//发送ajax请求 需要配代理
		axios.get('../../../data.json').then( res => {
			commit( TYPES.GET_GOODS , res.data.goods );
		})
	},
	showFoodDetail( { commit } , food ){
		commit( TYPES.SHOW_FOODDETAIL ,food );
	},
	hideFoodDetail( { commit } ){
		commit( TYPES.HIDE_FOODDETAIL );
	},
	showCartDetail( { commit } ,selectFoods){
		commit( TYPES.SHOW_CARTDDETAIL,selectFoods );
	},
	hideCartDetail( { commit } ){
		commit( TYPES.HIDE_CARTDDETAIL );
	},
	delSelected( { commit } ){
		commit( TYPES.HIDE_CARTDDETAIL );
	},
}

const mutations = {
	[TYPES.GET_GOODS](state,data){
		state.goods = data;

	},
	[TYPES.SHOW_FOODDETAIL](state,food){
		this.state.ratings.ratings = food.ratings;
		this.state.ratings.selectRatings = food.ratings;
		state.detailShow = true;
		state.food = food;
	},
	[TYPES.HIDE_FOODDETAIL](state,food){
		state.detailShow = false;

	},
	[TYPES.SHOW_CARTDDETAIL](state,selectFoods){
		if(selectFoods.length==0){
			return
		}
		state.selectFlag = true;
		state.select = selectFoods;
		console.log(state.select)
	},
	[TYPES.HIDE_CARTDDETAIL](state,food){
			state.selectFlag = false;
	},

}

export default {
	state,
	getters,
	mutations,
	actions
}


