<template>
	<div class="cartControl">
        <i class="iconfont icon-jian reduce" @click.stop='reduce' v-show="food.count>0"></i>
		<span class="count " v-show="food.count>0">{{food.count}}</span>
		<i class="iconfont icon-jia" @click.stop='add'></i>
	</div> 
</template>

<script>
	import Vue from 'vue'
	import { mapGetters } from 'vuex'
	export default{
		 computed : {
			...mapGetters([
				'select',
				'selectFlag',
				'goods'
			])
    	},
		props:['food'],
		methods:{
			add(){
				if( !this.food.count ){
					Vue.set(this.food , 'count' , 1);
				}else{
					this.food.count ++;
				}
			},
			reduce(){
				if( this.food.count ){
					this.food.count --;
					
					
				}
				let foods = [];
				// 遍历大的分类
				this.goods.forEach( good => {
					// 遍历分类里面的每一个商品
					good.foods.forEach( food => {
						// 如果商品的count大于0  就放进定义的数组中去
						if( food.count > 0 ){
							foods.push(food);
						}
					})
				});
					if(foods.length==0){
						this.$store.dispatch('hideCartDetail')
					}
			}
		}
	}
</script>

<style lang='less' scoped>
	
	.cartControl{
		display: inline-block;
        .icon-jia,.icon-jian{
            font-size: 0.48rem;
            color:rgb(0,160,220);

        }
        .count{
            font-size: 0.2rem;
            color: rgb(147,153,159);
            // line-height: 0.48rem;
            display: inline-block;
            width: 0.4rem;
            text-align: center;
            vertical-align: top;
            padding-top: 0.12rem;
        }
	}
</style>


