<template>
	<transition enter-active-class = 'animated tada' leave-active-class = 'animated tada'>	
		<div class="foodDetail" v-show='detailShow' ref='foodDetail'>
			<div>
				<div class="foodDetailImg"  @click='hideFoodDetail'>
					<img :src="food.image" alt="">
				</div>
				<div class="foodDetailInfo">
					<div class="foodDetailName">{{food.name}}</div>
					<div class="food-item-info-Count">
						<span class="food-item-info-sellCount">月售{{food.sellCount}}份</span>
						<span class="food-item-info-rating">好评率{{food.rating}}%</span>
					</div>
					<div class="food-item-info-sell">
						<div>
							<span class="food-item-info-price">￥{{food.price}}</span>
							<span v-show="food.oldPrice" class="food-item-info-oldPrice">￥{{food.oldPrice}}</span>
						</div>
						<div>
							<span class="add" v-show="!food.count" @click="addCount">加入购入车</span>
						</div>
						<cartControl :food='food' v-show="food.count>0"></cartControl>
					</div>
				</div>
				<!-- 空白连接处 -->
				<div class="whiteSpace"></div>	
				<div class="foodDetailDes">
					<div class="foodDetailDesTitle">商品介绍</div>
					<div class="foodDetailDesIntr">{{food.info}}</div>
				</div>
				<!-- 空白连接处 -->
				<div class="whiteSpace"></div>
				<!-- 评论筛选按钮 -->
				<div class="ratingButton">
					<div class="ratingTitle">商品评价</div>
					<ratingSelect :rate='food.ratings'></ratingSelect>
				</div>
				<div class="ratingList">
					<div class="ratingListItem" v-for="(v,k) in selectRatings" :key="k">
						<div class="rateUserInfo">
							<div class="rateTime">{{v.rateTime | time}}</div>
							<div class="rateUser">
								<span class="rateUsername">{{v.username}}</span>
								<img :src="v.avatar" alt="">
							</div>
						</div>
						<div class="rateInfo">
							<i class="iconfont" :class="v | sayyes"></i>
							<span class="rateInfoText">{{v.text}}</span>
						</div>
					</div>
				</div>
			</div>			
		</div>
	</transition>
</template>

<script>
	import Vue from 'vue'
	import time from '../../time'
	import { mapGetters } from 'vuex'
	import BScroll from 'better-scroll'
	import doyes from '../../doyes'
	import sayyes from '../../sayyes'
	import ratingSelect from '../ratingSelect/ratingSelect'
	import cartControl from '../cartControl/cartControl.vue'
	export default {
		components:{
			ratingSelect,
			cartControl
		},
		mounted(){
			this.$nextTick( () => {
				if( !this.foodDetailBScroll ){
					this.foodDetailBScroll = new BScroll( this.$refs.foodDetail,{
						click : true
					})
				}else{
					this.foodDetailBScroll.refresh();
				};
			}),
			this.$store.dispatch('changTypes',['推荐','吐槽','全部']);
		},
		computed : {
			...mapGetters([
				'detailShow',
				'food',
				'selectRatings'
			])
		},
		methods:{
			hideFoodDetail(  ){
				this.$store.dispatch('hideFoodDetail');
			},
			addCount(){
				if( !this.food.count ){
					Vue.set(this.food , 'count' , 1);
				}
			}
		}
	}

</script>

<style lang='less' scoped>
	.foodDetail{
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background: rgb(255,255,255);
		z-index: 100;
		.foodDetailImg{
			img{
				width: 100%;
				height: 7.5rem;
			}
		}
		.foodDetailInfo{
			padding: 0.36rem;
			box-sizing: border-box;
			.foodDetailName{
				font-size: 0.28rem;
				font-weight: 700;
				color: rgb(7,17,27);
			}
			.food-item-info-Count{
				margin-top: 0.16rem;
				font-size: 0.2rem;
				color: rgb(147,153,159);
				.food-item-info-sellCount{
					margin-right: 0.12rem;
				}
			}
			.food-item-info-sell{
				margin-top: 0.16rem;
				display: flex;
				justify-content: space-between;
				.food-item-info-price{
					font-size: 0.28rem;
					line-height: 0.48rem;
					font-weight: 700;
					color: rgb(240,20,20);
					margin-right: 0.08rem;
				}
				.food-item-info-oldPrice{
					font-size: 0.2rem;
					line-height: 0.48rem;
					font-weight: 700;
					text-decoration: line-through;
					color: rgb(147,153,159);
				}
				.add{
					display: inline-block;
					font-size: 0.2rem;
					height: 0.48rem;
					width: 1.48rem;
					border-radius: 0.24rem;
					line-height: 0.48rem;
					color:rgb(255,255,255);
					background-color:rgb(0,160,220);
					text-align: center;
				}
			}
		}
		/*空白连接处*/
		.whiteSpace{
			height: 0.34rem;
			background-color: rgb(233,235,236);
			border-bottom:0.01rem solid rgba(7,17,27,0.1) ;
			border-top:0.01rem solid rgba(7,17,27,0.1) ;
		}
		.foodDetailDes{
			padding: 0.36rem;
			box-sizing: border-box;
			.foodDetailDesTitle{
				font-size: 0.28rem;
				color: rgb(7,17,27);
			}
			.foodDetailDesIntr{
				padding: 0 0.16rem;
				box-sizing: border-box;
				margin-top: 0.12rem;
				font-size: 0.24rem;
				color: rgb(77,85,93);
				line-height: 0.48rem;
				font-weight: 200;
			}
		}
		/*评论筛选按钮*/
       .ratingButton{
			padding: 0.36rem 0.36rem 0 0.36rem;
			box-sizing: border-box;
			border-top:0.01rem solid #e6e5e2 ;
			border-bottom:0.01rem solid #e6e5e2 ; 
			.ratingTitle{
				font-size: 0.28rem;
				color: rgb(7,17,27);
			}
		}
		.ratingList{
			padding: 0 0.36rem;
			box-sizing: border-box;
			.ratingListItem{
				padding: 0.32rem 0;
				box-sizing: border-box;
				border-bottom:0.01rem solid #e6e5e2 ;
				.rateUserInfo{
					display: flex;
					justify-content: space-between;
					font-size: 0.2rem;
					color: rgb(147,153,159);
					line-height: 0.24rem;
					.rateUser{
						span{
							vertical-align: top;
							margin-right: 0.12rem;
						}
						img{
							height: 0.24rem;
							width: 0.24rem;
							border-radius: 0.12rem;
						}
					}
				}
				.rateInfo{
					margin-top: 0.06rem;
					font-size: 0.24rem;
					
					line-height: 0.32rem;
					color:rgb(0,160,220);
					.icon-down{
						color:rgb(147,153,159)
					}
					span.rateInfoText{
						color: rgb(7,17,27);
					}
				}
			}
		}	
	}
</style>


