<template>
  <!-- 评论开始 -->
  <div id="rating">
    <div class="rating">
      <!-- 评论分数 -->
      <div class="ratingScore">
        <!-- 综合分数 -->
        <div class="ratingScoreInfo">
          <p>{{seller.score}}</p>
          <p>综合评分</p>
          <p>高于周边商家{{seller.rankRate}}%</p>
        </div>
        <!-- 服务态度和菜品评分和送达时间 -->
        <div class="ratingScoreStar">
          <!-- 服务态度 -->
          <div class="ratingScoreStarList">
            <span>服务态度</span>
            <star :score="seller.serviceScore" ></star>
            <span>{{seller.serviceScore}}</span>
          </div>
          <!-- 菜品评分 -->
          <div  class="ratingScoreStarList">
            <span>菜品评分</span>
            <star :score="seller.foodScore"></star>
            <span>{{seller.foodScore}}</span>
          </div>
          <!-- 送达时间 -->
          <div class="ratingScoreStarList">
            <span>送达时间</span>
            <span class="deliveryTime">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>
      <!-- 空白连接处 -->
      <div class="whiteSpace"></div>
      <!-- 评论详情列表 -->
      <div class="ratingInfo">
        <!-- 评论筛选按钮 -->
        <div class="ratingButton">
          <ratingSelect :rate='ratings'></ratingSelect>
        </div>
        
        <!-- 评论内容列表 -->
        <div class="ratingInfoList">
          <!-- 评论内容列表元素 -->
          <div  v-for="(v,k) in selectRatings" :key="k" class="ratingInfoItem">
            <!-- 头像、用户名、等级 -->
            <div class="ratingInfoItemContent">
              <!-- 头像 -->
              <div class="ratingInfoItemHeader"><img :src="v.avatar" alt=""></div>
              <!-- 用户名、等级 -->
              <div class="ratingInfoItemScores">
                <!-- 用户名 -->
                <div class="ratingInfoItemName">
                  <div>{{v.username}}</div>
                  <div>{{v.rateTime  | time}}</div>
                </div>
                <!-- 等级 -->
                <div class="ratingInfoItemScore">
                  <star :score="v.score" :size="'small'"></star>
                  <span v-show="v.deliveryTime">{{v.deliveryTime}}分钟送达</span>
                </div>
              </div>       
            </div> 
            <!-- 评论内容   -->
            <div class="ratingInfoItemText">{{v.text}}</div>
            <!-- 评论菜品、满意与否 -->
            <div class="ratingInfoItemRecommends">
              <!-- 满意与否 -->
              <span>
                <i class="iconfont" :class="v | sayyes"></i>
              </span>  
              <!-- 评论菜品       -->
              <span v-for="(value,key) in v.recommend" :key="key" class="ratingInfoItemRecommend">{{value}}</span>
            </div>
          </div>                   
        </div>
      </div>
  </div>
  </div>
  
</template>
<script>
import {mapGetters,mapActions} from "vuex";
import star from '../star/star'
import time from '../../time'

import BScroll from 'better-scroll'
import ratingSelect from '../ratingSelect/ratingSelect'
export default {
  components:{
    star,
    ratingSelect
  },
  computed:{
    ...mapGetters([
        'seller',
        'ratings',
        'selectRatings'
    ]),
  },
  watch:{
    ratings(){
      this.$nextTick( () => {
        if( !this.sellerSCroll ){
          this.sellerSCroll = new BScroll( document.getElementById('rating') ,{
            click : true
          });
        }else{
          this.sellerSCroll.refresh();
        }
      }),
      this.$nextTick(()=>{
        this.$store.dispatch('changTypes',['满意','不满意','全部']);
      })
    }
  
  },
  mounted(){
    this.$nextTick(()=>{
        this.$store.dispatch('getRatings');
     })
  }
}
</script>
<style lang="less" scoped>
  /*评论开始*/
  #rating{
    position: absolute;
    z-index: -100;
    height: 3rem;
      .rating{
      /*评论分数*/
      .ratingScore{
        border-bottom: 0.01rem solid #e6e5e2;
        padding: 0.36rem 0;
        display: flex;
        /*综合分数*/
        .ratingScoreInfo{
          width: 2.75rem;
          height: 1.32rem;
          border-right: 0.01rem solid #e6e5e2;
          text-align: center;
          box-sizing: border-box;
          p:first-child{
            font-size: 0.48rem;
            color: rgb(255,153,0);
            line-height: 0.56rem;
          }
          p:nth-child(2){
            margin-top: 0.12rem;
            font-size: 0.24rem;
            color: rgb(7,17,27);
          }
          p:last-child{
            margin-top: 0.16rem;
            font-size: 0.2rem;
            color: rgb(147,153,159);
          }
        }
        /*服务态度和菜品评分和送达时间*/
        .ratingScoreStar{
          padding: 0 0.48rem;
          box-sizing: border-box;
          font-size: 0.24rem;
          color: rgb(7,17,27);
          line-height:0.36rem;
          /*服务态度*/
          .ratingScoreStarList{
            margin-bottom: 0.04rem;
            .star{
              display: inline-block;
            }
            span{
              vertical-align: top;
            }
            span:first-child{
              margin-right: 0.12rem;
            }
            span:last-child{
              color: rgb(255,153,0); 
            }
            /*送达时间*/
            span.deliveryTime{
              color: rgb(147,153,159);
            }
          }
        }
      }
      /*空白连接处*/
      .whiteSpace{
        height: 0.34rem;
        background-color: rgb(233,235,236);
        border-bottom:0.01rem solid #e6e5e2 
      }
      /*评论详情列表*/
      .ratingInfo{
        font-size: 0.24rem;
        color: rgb(7,17,27);
        .ratingButton{
          padding: 0 0.36rem;
            box-sizing: border-box;
            border-top:0.01rem solid #e6e5e2 ;
            border-bottom:0.01rem solid #e6e5e2 ; 
        }
        /*评论内容列表*/
        .ratingInfoList{
          padding: 0 0.36rem;
          box-sizing: border-box;
          border-top:0.01rem solid #e6e5e2 ;
          border-bottom:0.01rem solid #e6e5e2 ; 
          /*评论内容列表元素*/
          .ratingInfoItem{
            padding:  0.36rem 0;
            box-sizing: border-box;
            border-bottom:0.01rem solid #e6e5e2 ; 
            /*头像、用户名、等级*/
            .ratingInfoItemContent{
              display: flex;
              /*头像*/
              .ratingInfoItemHeader{
                img{
                  height: 0.56rem;
                  width: 0.56rem;
                  border-radius: 0.28rem;
                }
              }
              /*用户名、等级*/
              .ratingInfoItemScores{
                flex:1;
                /*用户名*/
                .ratingInfoItemName{
                  width: 100%;
                  padding-left: 0.14rem;
                  box-sizing: border-box;
                  vertical-align: middle;
                  display: flex;
                  justify-content: space-between;
                  font-size: 0.2rem;
                  color: rgb(7,17,27);
                  line-height: 0.24rem;
                  margin-bottom: 0.08rem;
                  div:last-child{
                    color: rgb(147,153,159);
                  }
                }
                /*等级*/
                .ratingInfoItemScore{
                    padding-left: 0.14rem;
                    .star{
                      display: inline-block;
                    }
                  span{
                    font-size: 0.2rem;
                    color: rgb(147,153,159);
                    line-height: 0.24rem;
                    font-weight: 200;
                    margin-left: 0.06rem;
                  }
                }

              }
            }
            /*评论内容*/
            .ratingInfoItemText{
              margin-top: 0.12rem;
              padding-left: 0.7rem;
              box-sizing: border-box;
              font-size: 0.24rem;
              color: rgb(7,17,27);
              line-height: 0.36rem;
            }
            /*评论菜品、满意与否*/
            .ratingInfoItemRecommends{
              margin-top: 0.16rem;
              padding-left: 0.7rem;
              box-sizing: border-box;
              /*满意与否*/
              span{
                font-size: 0.24rem;
                color:rgb(0,160,220);
                line-height: 0.32rem;
                vertical-align: middle;
                i{
                  font-size: 0.24rem;
                }
                .icon-down{
                  color:rgb(147,153,159)
                }
              }
              /*满意与否评论菜品*/
              .ratingInfoItemRecommend{
                display: inline-block;
                height: 0.32rem;
                width: 1.26rem;
                border: 0.01rem solid rgba(7,17,27,0.1);
                box-sizing: border-box;
                padding: 0 0.12rem;
                font-size: 0.18rem;
                color: rgb(147,153,159);
                line-height: 0.32rem;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                margin-right: 0.16rem;
              }
              .ratingInfoItemRecommend:nth-child(n+6){
                margin-left: 0.30rem;
                margin-top: 0.16rem;
              }
            }
            
          }
        }
      }
    }
  }
  
</style>



