import Vue from 'vue'

Vue.filter('doyes',function(value,data){
    let count =0;
    for(let i=0; i<value.length;i++){
      if(value[i].rateType==0){
        count++
      }
    }
    if(data=='yes'){
      return count;
    }
    return value.length-count
    
})


