<template>
    <!-- animate动画 -->
  	<transition enter-active-class = 'animated tada' leave-active-class = 'animated tada'>		
		<div class="sellerDetail" v-show='sellerShow'>
            <!-- 商家名称 -->
            <div class="sellerName">
                {{seller.name}}
            </div>
            <!-- 商家评分等级 -->
            <div class="sellerStar">
                <star :score="seller.score" :size="'big'"></star>
            </div>
            <!-- 商家优惠信息 -->
            <div class="Benefit">
                <div class="BenefitTitle">
                    <hr>
                    <span>优惠信息</span>
                    <hr>
                </div>
                <div class="BenefitInfo">
                    <div class="BenefitInfoList" v-for="(v,k) in seller.supports" :key="k">
                        <img :src="v | imgChoose" alt="">
                        <span >{{v.description}}</span>
                    </div>
                </div>
            </div>
            <!-- 商家公告信息 -->
            <div class="Benefit Notices">
                <div class="BenefitTitle NoticesTitle">
                    <hr>
                    <span>商家公告</span>
                    <hr>
                </div>
                <div class="BenefitInfo NoticesInfo">
                    <p>{{seller.bulletin}}</p>
                </div>
            </div>     
            <!-- 关闭按钮        -->
			<div class="close"  @click='hideSellerDetail'><i class="iconfont icon-guanbi"></i></div>
		</div>
        	
	</transition>
</template>
<script>    
    import { mapState,mapGetters,mapActions } from 'vuex'
    // 引用animate动画
    import animate from 'animate.css'
    import star from '../star/star'
    import imgChoose from '../../img'
export default {
    components:{
        star,
    },
    computed: mapGetters([
        'seller',
        'sellerShow'
    ]),
    methods:{
        // 将隐藏的事件传输到store 下的seller 中
        ...mapActions([
            'hideSellerDetail'
        ])
    },   
}
</script>
<style lang="less" scoped>
    /*设置页面整体的样式*/
	.sellerDetail{
		width: 100%;
		height: 100%;
        position: fixed;
        z-index: 10;
		left: 0;
		top: 0;
        background:rgba(7,17,27,0.8);
        padding: 1.28rem 0.72rem 0.64rem 0.72rem;
        box-sizing: border-box;
        color:rgb(255,255,255);
        text-align: center;
         /*商家名称*/
		.sellerName{
            color: white;
            text-align: center;
            font-size: 0.32rem;
            font-weight: 700;
        }
        /*商家评分等级*/
        .sellerStar{
            margin-top: 0.32rem;
            img{
               margin-right: 0.3rem;
               width: 0.42rem;
               height: 0.42rem; 
            }
        }
        /*商家优惠信息*/
        .Benefit{
            margin-top: 0.56rem;
            .BenefitTitle{
                width: 100%;
                hr{
                    width: 2.24rem;
                    border-color:rgba(255,255,255,0.2);
                    display: inline-block;
                }
                span{
                    font-size: 0.28rem;
                    padding: 0 0.1rem;
                    box-sizing: border-box;
                 }           
            }
            .BenefitInfo{
                text-align: left;
                margin-top: 0.48rem;
                padding-left: 0.24rem;
                box-sizing: border-box;
                .BenefitInfoList{
                    margin-top: 0.24rem;
                    img{
                        width: 0.32rem;
                        height:0.32rem;
                    }
                    span{
                        margin-left: 0.04rem;
                       font-size: 0.24rem;
                       font-weight: 200;
                        vertical-align: top;
                        line-height: 0.32rem;
                    }
                }
            }
        }
        /*商家公告信息*/
        .Notices{
            .NoticesInfo{
                p{
                    font-size: 0.24rem;
                    font-weight: 200;
                    line-height: 0.48rem;
                }
            }

        }
        /*关闭按钮*/
        .close{
            font-size: 0.64rem;
            color:rgba(255,255,255,0.5);
            position: absolute;
            bottom: 0.64rem;
            width: 100%;
            margin-left: -0.72rem;
            text-align: center;
        }
	}
</style>



