<template>
    <div class="contain" v-show="selectFlag">
        <div class="transparent" @click.stop.prevent="hideCartDetail"></div>

        <div class="detail">
            <div class="title">
                <span @click="hideCartDetail">购物车</span>
                <span @click="delSelected">清空</span>
            </div>
            <div class="sc">
                <div class="food" v-for="(v,k) in select" :key="k" v-show="v.count">
                    <span>{{v.name}}</span>
                    <span>¥{{v.price}}</span>
                    <cartControl :food = 'v' class="c2"></cartControl>
                </div>
            </div>
            
        </div>

    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import cartControl from '../cartControl/cartControl'
export default {
    components : {
        cartControl
    },
    computed : {
        ...mapGetters([
            'select',
            'selectFlag',
        ])
    },
    methods:{
        hideCartDetail(){
            // console.log(999999)
            this.$store.dispatch('hideCartDetail')
        },
        delSelected(){
            this.select.forEach( food => {
                food.count = 0;
            })
            this.$store.dispatch('delSelected')
        }
    }
}
</script>

<style lang="less" scoped>
    /* for Chrome */
    .detail::-webkit-scrollbar {
        display: none;
    }
    .contain{
        // display: flex;
        // flex-wrap: wrap ;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 10000;
        // border: 1px solid rgb(0,0,0);
        box-sizing: border-box;
        height: 12.18rem;
        display:flex;
		flex-direction:column;
        .transparent{
            // position: absolute;
            // top: 0;
            // right: 0;
            // bottom: 0;
            // left: 0;
            background-color: #000;
            opacity: .4;
            transition: opacity .3s ease;
            // background: rgba(7, 17, 27, 0.6);
            // width: 100%;
            flex:1;
        }
        .detail{
            max-height: 6rem;
            width: 100%;
            overflow-y: scroll;

            .title{
                height: 0.8rem;
                background: #f3f5f7;
                
            }
            span:first-child{
                font-size: .28rem;
                font-weight: 200;
                color: rgb(7, 17, 27);
                line-height: 0.8rem;
                margin-left: 0.36rem;
                display: inline-block;
                width: 1.6rem;
            }
            span:last-child{
                font-size: .24rem;
                color: rgb(0 , 160, 220);
                line-height: 0.8rem;
                margin-left: 4.5rem;
            }
        }
        .sc{
            .food{
                padding: 0.24rem 0.36rem;
                border-bottom: 1px solid rgba(7,17,27,0.1);
                background : white;
                height: 0.96rem;
                box-sizing: border-box;
                display: flex;
                justify-content: space-between;
                span:first-child{
                    margin: 0;
                    font-size: 0.28rem;
                    color: rgb(7, 17, 27);
                    line-height: .48rem;
                    width: 3rem;
                }
                span:nth-child(2){
                    font-size: 0.28rem;
                    font-weight: 700;
                    color: red;
                    line-height: 0.48rem;
                    margin-left: 1.4rem;

                }
                // .c2{
                //     vertical-align: top;
                // }
            }
        }
        
    }
</style>


