import axios from 'axios';
import TYPES from '../types.js'
const state = {
	seller : {},	//定义seller属性为对象
	sellerShow:false,
}
const getters = {		
	seller( state ){
		return state.seller;
	},
	sellerShow( state ){
		return state.sellerShow;
	},
}
const actions = {
  	//获取seller数据
	getSeller( {commit} ){
      //发送ajax请求 需要配代理
		axios.get('../../../data.json').then( res => {
			// console.log(res.data.seller);
			commit( TYPES.GET_SELLER , res.data.seller );
		})
	},
	showSellerDetail({commit}){
		commit( TYPES.SHOW_SELLER_DETAIL)
	},
	hideSellerDetail({commit}){
		commit( TYPES.HIDE_SELLER_DETAIL)
	},
}
const mutations = {	
    //获取seller数据
	[TYPES.GET_SELLER](state,data){
		state.seller = data;
	},
	[TYPES.SHOW_SELLER_DETAIL](state){
		// console.log(1111)
		state.sellerShow=true;
	},
	[TYPES.HIDE_SELLER_DETAIL](state){
		// console.log(222)
		state.sellerShow=false;
	},
}
export default {
	state,
	getters,
	mutations,
	actions
}


