<template>
    <!-- 头部开始 -->
    <header>
        <div class="sellerHeader"  >
            <!-- 设置头部背景图片 -->
            <img class="sellerHeaderBg"  :src="seller.avatar" alt="">
            <!-- 设置头部上半部分背景透明度 -->
            <div class="sellerInfoBg">         
            </div>
            <!-- 设置头部下半部分背景透明度 -->
            <div class="sellerNoticeBg"></div>
            <!-- 头部上半部分商家信息 -->
            <div class="sellerInfo">
                <!-- 商家图片 -->
                <img class="sellerInfoImg" @click="showSellerDetail" :src="seller.avatar" alt="">
                <!-- 商家名称，送餐信息，优惠活动 -->
                <div class="sellerInfoDefine">
                    <div class="sellerInfoDefineName">
                        <img class="sellerInfoDefineNameImg" src="../../../static/img/brand.png" alt="">
                        <p>{{seller.name}}</p>
                    </div>
                    <p class="sellerInfoDefineTime">{{seller.description}}/{{seller.deliveryTime}}分钟送达</p>
                    <div class="sellerInfoDefineBenefit">
                        <img class="sellerInfoDefineBenefitImg" src="../../../static/img/decrease.png" alt="">
                        <p v-if="seller.supports">{{seller.supports[0].description}}</p>
                    </div>
                </div>
                <!-- 查看更多优惠活动 -->
                <div class="sellerInfoDefineBenefitMore"  @click="showSellerDetail">
                    <span v-if="seller.supports">{{seller.supports.length}}个</span>
                    <span>></span>
                </div>
            </div> 
            <!-- 商家公告 -->
            <div class="sellerNotice">
                <img class="sellerNoticeBulletinImg" src="../../../static/img/bulletin.png" alt="">
                <span class="sellerNoticeBulletin">{{seller.bulletin}}</span>
                <span>></span>
            </div>
            <sellerDetail></sellerDetail>
        </div>
    </header>
    <!-- 头部结束 -->
</template>
<script>
// 加载并解析vuex
import {mapGetters,mapActions} from "vuex";
// 加载sellerDetail组件
import sellerDetail from '../sellerDetail/sellerDetail'
export default {
    components:{
        sellerDetail
    },
    computed:{
        ...mapGetters([
            'seller'
        ])
    },
    // 将显示商家详情的事件传输到store 下的seller 中
    methods:{
        ...mapActions([
            'showSellerDetail'
        ])
    }
}
</script>
<style scoped lang="less">
    // 头部开始
    .sellerHeader{
        width: 100%;
        height: 2.68rem;
        position: relative;
        // 设置头部背景图片
        .sellerHeaderBg{
            width: 100%;
            height: 2.68rem;
            position: absolute;
            z-index: -5;
            filter: blur(0.1rem);
        }
        // 设置头部上半部分背景透明度
        .sellerInfoBg{
            height: 2.12rem;
            width: 100%;
            position: absolute;
            background: rgba(7,17,27,0.5);
        }
        // 设置头部下半部分背景透明度
        .sellerNoticeBg{
            height: 0.56rem;
            width: 100%;
            position: absolute;
            bottom: 0;
            background: rgba(7,17,27,0.7);
        }
        // 头部上半部分商家信息
        .sellerInfo{
            width: 100%;
            height: 2.12rem;
            box-sizing: border-box;
            padding-top: 0.48rem;
            padding-left: 0.48rem;
            position: absolute;
            z-index: 1;
            // 商家图片
            .sellerInfoImg{
                height: 1.28rem;
                width: 1.28rem;
                border-radius:0.04rem;
                display: inline-block;
            }
            // 商家名称，送餐信息，优惠活动
            .sellerInfoDefine{
                margin-left: 0.32rem;
                height: 1.28rem;
                box-sizing: border-box;
                padding: 0.04rem 0 0.04rem 0;
                display: inline-block;
                vertical-align:top;
                .sellerInfoDefineName{
                    .sellerInfoDefineNameImg{
                        height: 0.36rem;
                        width:  0.6rem;
                        display: inline-block;
                    }
                    p{
                        vertical-align:top;
                        display: inline-block;
                        font-size: 0.32rem;
                        color: rgb(255,255,255);
                        line-height: 0.36rem;
                        font-weight: bold;
                    }
                }
                .sellerInfoDefineTime{
                    font-size: 0.24rem;
                    color: rgb(255,255,255);
                    line-height: 0.24rem;
                    font-weight: 200;
                    margin-top: 0.16rem;
                }
                .sellerInfoDefineBenefit{
                    margin-top: 0.2rem;
                    .sellerInfoDefineBenefitImg{
                        height: 0.24rem;
                        width:  0.24rem;
                        display: inline-block;
                    }
                    p{
                        vertical-align:top;
                        display: inline-block;
                        font-size: 0.2rem;
                        color: rgb(255,255,255);
                        line-height: 0.24rem;
                        font-weight: 200;

                    }
                }
            }
            // 查看更多优惠活动
            .sellerInfoDefineBenefitMore{
                background-color: rgba(0,0,0,0.2);
                border-radius: 0.48rem;
                padding: 0.14rem 0.16rem;
                box-sizing: border-box;
                width: 0.88rem;
                height: 0.48rem;  
                font-size: 0.2rem;
                color: rgb(255,255,255);
                font-weight: 200; 
                position: absolute; 
                right: 0.24rem;
                bottom: 0.3rem; 
                display: flex;
                justify-content: space-between;       
                span{
                    vertical-align: top;
                }
            }
        }
        // 商家公告
        .sellerNotice{
            position: absolute;
            bottom: 0;
            font-size: 0.2rem;
            color: rgb(255,255,255);
            width: 100%;
            line-height: 0.24rem;
            font-weight: 200;
            display: flex;
            vertical-align: top;
            padding: 0.16rem 0.24rem;
            box-sizing: border-box;
            justify-content:space-between;
            .sellerNoticeBulletinImg{
                width: 0.44rem;
                height: 0.24rem;
            }
            .sellerNoticeBulletin{
                overflow: hidden;
                white-space: nowrap;
                text-overflow:ellipsis;
                display: inline-block;
                vertical-align: top;
                padding: 0 0.08rem;
            }
        }
    }
    // 头部结束
</style>



