<template>
	<div class="ratingButtontotal">
        <div class="ratingButtonList">
            <span  @click='select(2)' :class="{'active' :selectType==2 }" class="all" v-if="rate">{{types[2]}}<small>{{rate.length}}</small></span>
            <span @click='select(0)' :class="{'active' :selectType==0 }" class="good" v-if="rate">{{types[0]}}<small>{{rate | doyes('yes')}}</small></span>
            <span @click='select(1)' :class="{'active' :selectType==1 }" class="bad" v-if="rate">{{types[1]}}<small>{{rate | doyes('no')}}</small></span>
        </div>
        <div class="ratingButtonChoose"  @click='toggle'>
            <span :class='{"on" : onlyContent}'><i class="iconfont icon-103"></i></span>
            <span :class='{"on" : onlyContent}'>只看有内容的评价</span>
        </div>
        <!-- {{food}} -->
	</div>

</template>

<script>
	
	import { mapGetters } from 'vuex'
    import doyes from '../../doyes'
    import sayyes from '../../sayyes'
	export default{
        props:[
			'rate'
		],
		computed:{
			...mapGetters([
				'types',
				'selectType',
                'onlyContent',
                'food'
			])
		},
		methods:{
			select( type ){
				this.$store.dispatch('select',{type});
			},
			toggle(){
				this.$store.dispatch('toggle');
			}
		},

	}
</script>

<style lang='less' scoped>
        .ratingButtontotal{
            .ratingButtonList{
                padding: 0.36rem 0;
                box-sizing: border-box;
                border-bottom:0.01rem solid #e6e5e2 ;
                span{
                display: inline-block;
                height: 0.64rem;
                width: 1.20rem;
                line-height: 0.64rem;
                text-align: center;
                margin-right: 0.06rem;
                small{
                    font-size: 0.12rem;
                    margin-left: 0.08rem;
                }
                }
                .all{
                    background-color: rgb(0,160,220);
                    &.active{
                        color:red;
                    }
                }
                .good{
                    background-color: rgb(204,236,248);
                    &.active{
                        color:red;
                    }
                }
                .bad{
                    width: 1.44rem;
                    background-color: rgb(233,235,236);
                    &.active{
                        color:red;
                    }
                }
            }
          .ratingButtonChoose{
            padding: 0.36rem 0;
            box-sizing: border-box; 
            vertical-align: middle;
            span{
                color: rgb(180,184,187);
                line-height: 0.4rem;
                vertical-align: top;
                &.on{
                    color:red;
                }
                i{
                    font-size: 0.4rem;  
                }
            }
            span:first-child{
              margin-right: 0.08rem;
            }
            span:last-child{
              
              font-size: 0.24rem;
            }
          }
        }
	
</style>


