import Vue from 'vue'
/* eslint-disable no-new */
Vue.filter('imgChoose', function(value){
    if(value.type==0){
        return '../../../static/img/decrease.png'
    }
    if(value.type==1){
        return "../../../static/img/discount.png"
    }
    if(value.type==2){
        return "../../../static/img/special.png"
    }
    if(value.type==3){
        return "../../../static/img/invoice.png"
    }
    if(value.type==4){
        return "../../../static/img/guarantee.png"
    }

});


