import Vue from 'vue'
import Router from 'vue-router'

import appGoods from  '../components/appGoods/appGoods.vue'
import appRating from  '../components/appRating/appRating.vue'
import appSeller from  '../components/appSeller/appSeller.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/goods',
      component: appGoods
    },
    {
      path: '/ratings',
      component: appRating
    },
    {
      path: '/seller',
      component: appSeller
    },
    {
      path:'*',
      redirect: '/goods'
    }
  ],
  linkActiveClass:'active'
})



